<template>
  <v-container :fluid="true">
    <UpdateCategoryDialog :category="category" @toggleUpdate="update" />
    <v-row>
      <v-col cols="12">
        <h1 class="font-weight-bold mb-3">Все категории</h1>
        <v-breadcrumbs class="px-0 py-0 mb-3" :items="items" divider="/">
          <template v-slot:item="{ item }">
            <v-breadcrumbs-item
              :disabled="item.disabled"
              :exact="true"
              :to="{ name: item.to.name }"
            >
              {{ item.text.toUpperCase() }}
            </v-breadcrumbs-item>
          </template>
        </v-breadcrumbs>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <div class="mb-3">
          <CategoriesTable
            @toggleUpdateCategory="toggleUpdateCategory"
            :categories="categories"
          />
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CategoriesTable from "../../components/Tables/CategoriesTable";
import UpdateCategoryDialog from "../../components/Dialogs/category/UpdateCategoryDialog";

export default {
  name: "Categories",
  metaInfo: {
    title: "Глазурь | Категории"
  },
  data() {
    return {
      items: [
        {
          text: "Панель",
          disabled: false,
          to: { name: "Home" }
        },
        {
          text: "Все категории",
          disabled: true,
          to: { name: "Categories" }
        }
      ]
    };
  },
  methods: {
    ...mapActions({
      loadCategories: "fetchCategories",
      loadCategory: "fetchCategory",
      updateCategory: "fetchUpdateCategory",
      show: "show"
    }),
    async toggleUpdateCategory(id) {
      await this.loadCategory(id);
      this.show("category:update");
    },
    async update({ id, data }) {
      await this.updateCategory({ id, data });
      await this.loadCategories();
      this.show("category:update");
    }
  },
  computed: {
    ...mapGetters({
      categories: "getCategories",
      category: "getCategory"
    })
  },
  components: {
    CategoriesTable,
    UpdateCategoryDialog
  },
  async beforeMount() {
    await this.loadCategories();
  }
};
</script>

