<template>
  <v-dialog v-model="dialog" width="600">
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="primary lighten-2" dark v-bind="attrs" v-on="on">
        Создать
      </v-btn>
    </template>

    <v-card>
      <v-card-title>
        <span class="text-h5">Создание категории</span>
      </v-card-title>

      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-text-field v-model="form.name" label="Название" />
            </v-col>
            <v-col cols="12">
              <v-checkbox
                v-model="form.is_show"
                :label="form.is_show ? 'Показывать' : 'Скрывать'"
              ></v-checkbox>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" @click="create">
          Создать
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "CreateCategoryDialog",
  data() {
    return {
      dialog: false,
      form: {
        name: "",
        is_show: true
      }
    };
  },
  methods: {
    ...mapActions({
      createCategory: "fetchCreateCategory",
      loadCategories: "fetchCategories"
    }),
    async create() {
      await this.createCategory(this.form);
      await this.loadCategories();
      this.close();
      this.clearForm();
    },
    close() {
      this.dialog = false;
    },
    clearForm() {
      this.form.name = "";
    }
  }
};
</script>

<style scoped></style>
