<template>
  <v-dialog
    v-model="$store.state.dialogs.dialogs.category.update"
    width="600"
    @click:outside="close"
  >
    <v-card>
      <v-card-title>
        <span class="text-h5">Обновление категории</span>
      </v-card-title>

      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-text-field v-model="form.name" label="Название" />
            </v-col>
            <v-col cols="12">
              <v-checkbox
                v-model="form.is_show"
                :label="form.is_show ? 'Показывать' : 'Скрывать'"
              ></v-checkbox>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" @click="update">
          Обновить
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "UpdateCategoryDialog",
  data() {
    return {
      form: { name: "" },
      dialog: false
    };
  },
  methods: {
    update() {
      this.$emit("toggleUpdate", {
        id: this.form.id,
        data: this.form
      });
    },
    close() {
      this.$emit("outside", false);
    }
  },
  computed: {
    parametersCategory: {
      get() {
        return this.$props.category;
      }
    }
  },
  watch: {
    category: {
      immediate: true,
      handler(update) {
        for (let prop in update) {
          this.form[prop] = update[prop];
        }
      }
    }
  },
  props: {
    category: {
      type: Object
    }
  }
};
</script>
