<template>
  <v-data-table
    v-bind="$attrs"
    :headers="headers"
    :items="categories"
    :options="{ page: 1, itemsPerPage: 10 }"
    :page="filter.page"
    v-on:pagination="updatePaginate"
    class="elevation-4"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Все категории</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>

        <v-spacer></v-spacer>

        <CreateCategoryDialog />
      </v-toolbar>
    </template>

    <template v-slot:item.id="{ item }">
      {{ item.id }}
    </template>

    <template v-slot:item.name="{ item }">
      {{ item.name }}
    </template>

    <template v-slot:item.price="">
      Включено
    </template>

    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editCategory(item.id)">
        mdi-pencil
      </v-icon>
      <v-icon small @click="deleteProduct(item.id)">
        mdi-delete
      </v-icon>
    </template>
  </v-data-table>
</template>

<script>
import CreateCategoryDialog from "../../components/Dialogs/category/CreateCategoryDialog";

export default {
  name: "CategoriesTable",
  inheritAttrs: false,
  data() {
    return {
      headers: [
        {
          text: "Номер",
          align: "start",
          sortable: false,
          value: "id"
        },
        {
          text: "Название категории",
          sortable: false,
          value: "name"
        },
        {
          text: "Отображение",
          sortable: false,
          value: "price"
        },
        {
          text: "Действия",
          sortable: false,
          value: "actions"
        }
      ],
      filter: {
        page: 1,
        pageCount: 3,
        itemsPerPage: 20
      }
    };
  },
  methods: {
    updatePaginate(paginate) {
      this.$emit("updatePaginate", paginate);
    },
    editCategory(id) {
      this.$emit("toggleUpdateCategory", id);
    }
  },
  components: {
    CreateCategoryDialog
  },
  props: {
    categories: {
      type: Array,
      required: true
    }
  }
};
</script>

<style scoped></style>
